import React from 'react';
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from 'mdbreact';
import { Container, makeStyles } from '@material-ui/core';
import { useFormik } from 'formik';

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(5, 2),
        paddingLeft: '30%',
        paddingTop: '10%',
        backgroundImage: 'https://images.unsplash.com/photo-1588529726892-93b3649b15c3?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max'
    }
}));
const SignIn1 = () => {
    const classes = useStyles();

    const formik = useFormik({
        initialValues: {
            email: '',
            password: ''
        },
        onSubmit: values => {
            console.log('Login Request1 ', JSON.stringify(values));
            alert("Login Request1 " + JSON.stringify(values, null, 2))
        }
    });

    return (
        <Container maxWidth="lg" className={classes.root}>
            <div>
                <MDBContainer>
                    <MDBRow>
                        <MDBCol md="6">
                            <form onSubmit={formik.handleSubmit} noValidate>
                                <p className="h5 text-center mb-4">Sign in</p>
                                <div className="grey-text">
                                    <MDBInput
                                        label="Type your email"
                                        icon="envelope"
                                        type="text"
                                        name="email"
                                        autoComplete="email"
                                        onChange={formik.handleChange}
                                        value={formik.values.email}                                        
                                    />
                                    <MDBInput
                                        label="Type your password"
                                        icon="lock"
                                        type="password"
                                        name="password"
                                        autoComplete="password"
                                        onChange={formik.handleChange}
                                        value={formik.values.password} validate
                                    />
                                </div>
                                <div className="text-center">
                                    <MDBBtn>Login</MDBBtn>
                                </div>
                            </form>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
            </div>
        </Container>
    );
};

export default SignIn1;