import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/styles";
import firebase from 'firebase';
import TableBody from '@material-ui/core/TableBody';
import { Paper, Table, TableCell, TableHead, TablePagination, TableRow } from "@material-ui/core";


const columns = [
    {
        id: 'firstName', label: 'First Name', minWidth: 50
    },
    {
        id: 'lastName', label: 'Last Name', minWidth: 50
    },
    {
        id: 'email', label: 'Email', minWidth: 50, align: 'right'
    },
    {
        id: 'mobile', label: 'Mobile', minWidth: 50, align: 'right'
    },
    {
        id: 'locationFrom', label: 'Dource', minWidth: 50, align: 'right'
    },
    {
        id: 'locationTo', label: 'Destination', minWidth: 50, align: 'right'
    },
];
const useStyles = makeStyles(theme => ({    
    root: {
            flexGrow: 1, 
            // minHeight: '80vh',
            backgroundColor: '#f4f6f8',        
        },
}));

const OrderRequests = (props) => {
    const classes = useStyles();
    const [orders, setOrder] = useState([]);
    
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(8);

    function handleChangePage(event, newPage) {
        setPage(newPage);
    }

    function handleChangeRowsPerPage(event) {
        setRowsPerPage(+event.target.value);
        setPage(0);
    }

    useEffect(() => {
        const todoRef = firebase.database().ref('Orders');
        todoRef.on('value', (snapshot) => {
          const todos = snapshot.val();
          const todoList = [];
          for (let id in todos) {
            todoList.push({ id, ...todos[id] });
          }
          setOrder(todoList);          
        });
      }, []);

    return (
        <div className={classes.root}>
            <div>
                <Paper className={classes.root}>
                    <div className={classes.tableWrapper}>
                        <Table stickyHeader>
                            <TableHead>
                                <TableRow>
                                    {columns.map(column => (
                                        <TableCell
                                            key={column.id}
                                            align={column.align}
                                            style={{ minWidth: column.minWidth }}>
                                            {column.label}
                                        </TableCell>
                                    ))}
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {orders.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(row => {
                                    return (
                                        <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                            {columns.map(column => {
                                                const value = row[column.id];
                                                return (
                                                    <TableCell key={column.id} align={column.align}>
                                                        {column.format && typeof value === 'number' ? column.format(value) : value}
                                                    </TableCell>
                                                );
                                            })}
                                        </TableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </div>
                    <TablePagination
                        rowsPerPageOptions={[10, 25, 100]}
                        component="div"
                        count={orders.length}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        backIconButtonProps={{
                            'aria-label': 'previous page',
                        }}
                        nextIconButtonProps={{
                            'aria-label': 'next page',
                        }}
                        onChangePage={handleChangePage}
                        onChangeRowsPerPage={handleChangeRowsPerPage} />
                </Paper>
            </div>
        </div>
    );
}
export default OrderRequests;