import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Footer from '../components/Footer/Footer'
import { useFormik } from "formik";
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import Axios from "axios";

const useStyles = makeStyles(theme => ({
    "@global": {
        body: {
            backgroundColor: theme.palette.common.white
        }
    },
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center"
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main
    },
    form: {
        width: "100%",
        marginTop: theme.spacing(3),
        minHeight: '40%'
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },
    error: {
        color: 'red',
        paddingTop: '5px',
        fontWeight: 'bold',
        paddingLeft: '2px'
    }
}));

const SignUp = (props) => {
    const classes = useStyles();

    const redirectToLogin = () => {
        console.log("you are just loggedIn")
        props.history.push(`/admin/login`)
    }

    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
            mobile: '',
            username: '',
            password: ''
        },
        onSubmit: values => {
            console.log('Signup request ', values);
            Axios.post('http://localhost:9090/public/signup', values).then((response) => {
                console.log(JSON.stringify(response.data));
                console.log('====================================');
                toast.info('Account created successfully !!!', { position: toast.POSITION.TOP_RIGHT });
                redirectToLogin();
            }).catch(error => {
                console.log('====================================');               
                toast.error("Signup error | Please try Later" , { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
            })      
        },
        validate: values => {
            let errors = {};
            if (!values.firstName)
                errors.firstName = "First Name is required";
            if (!values.lastName)
                errors.lastName = "Last Name is required";
            if (!values.email)
                errors.email = "Email is required";
            else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email))
                errors.email = "Invalid email address";
            if (!values.mobile)
                errors.mobile = "Mobile is required";
            if (!values.username)
                errors.username = "Username is required";
            if (!values.password)
                errors.password = "Password is required";
            else if (values.password.length < 5)
                errors.password = "Password is too short";
            return errors;
        }
    })
    // console.log("Form data",formik.values);

    return (
        <Container component="main" maxWidth="sm">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    {/* <LockOutlinedIcon /> */}
                </Avatar>
                <Typography component="h1" variant="h5">Sign up</Typography>
                <form className={classes.form} noValidate onSubmit={formik.handleSubmit}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                fullWidth
                                variant="outlined"
                                id="firstName"
                                name="firstName"
                                label="First Name"
                                autoComplete="firstName"
                                onChange={formik.handleChange}
                                value={formik.values.firstName}
                                autoFocus
                            />
                            {formik.errors.firstName ? <div className={classes.error}>{formik.errors.firstName}</div> : null}
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                fullWidth
                                variant="outlined"
                                id="lastName"
                                name="lastName"
                                label="Last Name"
                                autoComplete="lastName"
                                onChange={formik.handleChange}
                                value={formik.values.lastName}
                            />
                            {formik.errors.lastName ? <div className={classes.error}>{formik.errors.lastName}</div> : null}
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                fullWidth
                                variant="outlined"
                                id="mobile"
                                name="mobile"
                                label="Mobile"
                                autoComplete="mobile"
                                onChange={formik.handleChange}
                                value={formik.values.mobile}
                            />
                            {formik.errors.mobile ? <div className={classes.error}>{formik.errors.mobile}</div> : null}
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                fullWidth
                                variant="outlined"
                                id="email"
                                name="email"
                                label="Email Address"
                                autoComplete="email"
                                onChange={formik.handleChange}
                                value={formik.values.email}
                            />
                            {formik.errors.email ? <div className={classes.error}>{formik.errors.email}</div> : null}
                        </Grid>

                        <Grid item xs={12}>
                            <TextField
                                fullWidth
                                variant="outlined"
                                id="username"
                                name="username"
                                label="Username"
                                type="text"
                                autoComplete="current-username"
                                onChange={formik.handleChange}
                                value={formik.values.username}
                            />
                            {formik.errors.username ? <div className={classes.error}>{formik.errors.username}</div> : null}
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                fullWidth
                                variant="outlined"
                                id="password"
                                name="password"
                                label="Password"
                                type="password"
                                autoComplete="current-password"
                                onChange={formik.handleChange}
                                value={formik.values.password}
                            />
                            {formik.errors.password ? <div className={classes.error}>{formik.errors.password}</div> : null}
                        </Grid>
                        {/* <Grid item xs={12}>
                            <FormControlLabel
                                control={<Checkbox value="allowExtraEmails" color="primary" />}
                                label="I want to receive inspiration, marketing promotions and updates via email."
                            />
                        </Grid> */}
                    </Grid>
                    <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit}>Sign Up</Button>
                    <Grid container justify="flex-end">
                        <Grid item>
                            <Link to="/admin/login">Already have an account? Sign in</Link>
                        </Grid>
                    </Grid>
                </form>
            </div>
            <Footer />
        </Container>
    );
}
export default SignUp