import React from 'react';
import AwesomeSlider from "react-awesome-slider"
import 'react-awesome-slider/dist/styles.css';
import './Slider.css'

import 'react-awesome-slider/dist/custom-animations/cube-animation.css';
import { makeStyles } from '@material-ui/core';
const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1, 
        minHeight: '50vh',
        
    },bgcolor:{
        backgroundColor: 'white'
    },
    image:{
        width:'100%',
        
    }
}));
const Slider = () => {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <AwesomeSlider animation="cubeAnimation">
                {/* <div className={classes.image} data-src="6.jpg" />
                <div className={classes.image} data-src="reliable-packers.jpg" /> */}
                {/* <div className={classes.image} data-src="Options.jpg" /> */}
                <div className={classes.image} data-src="3.jpg" />
                <div className={classes.image} data-src="5.jpg" />                
            </AwesomeSlider>
        </div>
    );
};

export default Slider;