import React, { useState, useEffect } from 'react';
import { Container, Table, TableHead, TableRow, TableCell, TableBody, makeStyles } from '@material-ui/core';
import Navigation from '../Navigation/Navigation';
import Footer from '../Footer/Footer';
import Axios from 'axios';

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(2, 2),
        width: '100%',
        minHeight: '100vh'
    }
}));

const EnquiryList = () => {
    
    const classes = useStyles();
    const [enquiries, setEnquiries] = useState([]);

    useEffect(() => {        
        Axios.get('https://api.randomuser.me/?results=10').then((response) => {            
            setEnquiries(response.data.results);
        });
    }, [])
       
    
    const userData = enquiries.map((user) => {
        return (<TableRow key={user.login.uuid}>
            <TableCell >{user.login.uuid}</TableCell>
            <TableCell>{user.name.first}</TableCell>
            <TableCell>{user.name.last}</TableCell>
            <TableCell>{user.email}</TableCell>
            <TableCell>{user.phone}</TableCell>
            <TableCell><img src={user.picture.large} alt='' /></TableCell>
        </TableRow>)
    })
    return (
        <div>
            <Navigation />
            <Container maxWidth="xl">
                <div className={classes.root}>
                    <Table >
                        <TableHead>
                            <TableRow>
                                <TableCell >#Id</TableCell>
                                <TableCell >First Name</TableCell>
                                <TableCell >Last Name</TableCell>
                                <TableCell >Email Id</TableCell>
                                <TableCell >Mobile</TableCell>
                                <TableCell >Profile</TableCell>                                
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {/* {
                                userData.length > 0 ?
                                    { userData } : <tr><td> <font>No Data Found</font></td></tr>
                            } */}
                            {userData}
                        </TableBody>
                    </Table>
                </div>
            </Container>
            <Footer />
        </div>
    )
    
}
export default EnquiryList;
