import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Paper } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    body: {
        padding: '8px',
        margin: '8px',
        textAlign: "left",
        // minHeight: '100vh'
    },
    heading: {
        color: 'gray',
        fontSize: '20px',
        fontWeight: 'bold',
        textAlign: "left"        
    }

}));

function OfficeAddress() {
    const classes = useStyles();
    return (
        <Paper>
            <div className={classes.body}>                
                <Typography className={classes.heading}>Mywebsite</Typography>
                <p>Address: Abcd Xyz New Delhi-110061</p>                
                <p>Phone: +91-7828588330</p>
                <p>Email Address: info@Mywebsite.com</p>
                Website: www.Mywebsite.com
            </div>
        </Paper>
    );
}
export default OfficeAddress;
