import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";

import { Switch, Route, BrowserRouter as Router } from "react-router-dom";
import * as serviceWorker from './serviceWorker';
import Home from './components/Home/Home';
import About from './components/About/About';
import Services from './components/Services/Services';
import ContactUs from './components/ContactUs/ContactUs';
import SignIn from './SignIn/SignIn';
import SignUp from './SignUp/SignUp';
import TabBar from './components/Tabs/TabBar';
import Forgetpassword from './Forgetpassword/Forgetpassword';
import Table from './components/Table/Table';
import Formdata from './components/Table/Formdata';
import UpdateData from './components/Table/MaterialTableDemo';
import SignInSide from './SignIn/SignInSide';
import Album from './components/Album/Album';
import Example from './components/Pagination/Example';
import ListingPagination from './components/Pagination/ListingPagination';
import SimplePaging from './components/Pagination/SimplePaging';
import Gallery from './components/Gallery/Gallery';
import Dashboard from './components/Dashboard/Dashboard';
import Order from './components/Dashboard/Order';
import SignIn1 from './SignIn/SignIn1'
import EnquiryList from './components/EnquiryList/EnquiryList';
import Help from './components/Help/Help';
import PhotoDetail from "./components/Gallery/photoDetail"
import Feedback from './components/Feedback/Feedback';
import SignIn2 from './SignIn/SignIn2';
import ReactToastifyExample from './pages/experiment/ReactToastifyExample';
import ToastComponent from './pages/experiment/ToastComponent';
import NewForm from './pages/experiment/NewForm';
import UserList from './components/EnquiryList/UserList';
import PageNotFound from './NotFound/PageNotFound';
import ImageUpload from './firebase/ImageUpload';
import SimpleSlider from './components/Slider/SimpleSlider';
import UserForm from './firebase/GalleryData/UserForm';
import UserDataDetails from './firebase/GalleryData/UserDataDetails';
import { MyOrders } from './components/MyOrders/MyOrders';
import { Users } from './firebase/GalleryData/Users';

const app = (
    <Router>
        <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/about" component={About} />
            <Route exact path="/services" component={Services} />
            <Route exact path="/gallery" component={Gallery} />
            <Route exact path="/contact" component={ContactUs} />
            <Route exact path="/help" component={Help} />
            <Route exact path="/upload" component={ImageUpload} />
            
            <Route exact path="/orders" component={MyOrders} />
            <Route exact path="/users" component={Users} />
            <Route exact path="/users/:id" component={UserDataDetails} />
            
            <Route exact path="/dashboard" component={Dashboard} />
            <Route exact path="/myorder" component={Order} />
            <Route exact path="/enquiries" component={EnquiryList} />

            <Route exact path="/userList" component={UserList} />
            <Route exact path="/userForm" component={UserForm} />
            


            <Route exact path="/admin/login" component={SignIn} />
            <Route exact path="/admin/signup" component={SignUp} />
            <Route exact path="/admin/forgetpassword" component={Forgetpassword} />

            <Route exact path="/signin" component={SignInSide} />
            <Route exact path="/signin1" component={SignIn1} />
            <Route exact path="/signin2" component={SignIn2} />

            <Route exact path="/example" component={Example} />

            <Route exact path="/tab" component={TabBar} />
            <Route exact path="/table" component={Table} />
            <Route exact path="/form" component={UpdateData} />
            <Route exact path="/formdata" component={Formdata} />
            <Route exact path="/album" component={Album} />
            <Route exact path="/listing" component={ListingPagination} />
            <Route exact path="/paging" component={SimplePaging} />

            <Route exact path="/gallery/:photoId" component={PhotoDetail} />
            <Route exact path="/feedback" component={Feedback} />
            <Route exact path="/toast1" component={ReactToastifyExample} />
            <Route exact path="/toast" component={ToastComponent} />

            <Route exact path="/newForm" component={NewForm} />
            <Route exact path="/simpleSlider" component={SimpleSlider} />

            <Route component={PageNotFound} />
        </Switch>
    </Router>
);

ReactDOM.render(app, document.getElementById('root'));
// ReactDOM.render(<Home logoTitle="Mywebsite" />, document.getElementById('root'));
serviceWorker.unregister();
