import React, { useEffect, useState } from "react";

import { makeStyles } from "@material-ui/styles";
import { Container } from "@material-ui/core";
import firebase from '../index';
import Footer from "../../components/Footer/Footer";
import Navigation from "../../components/Navigation/Navigation";


const useStyles = makeStyles(theme => ({
    main: {
        minHeight: '100vh',        
        alignContent: 'center'
    },
    header: {
        fontWeight: 'bold'
    },
    content: {
        paddingLeft: '10px',
        marginTop: '10px'
    },
    image: {
        width: '40%',
        height: "10%"
    }

}));

const  UserDataDetails = (props) => {

    console.log(props)
    const classes = useStyles();
    const [todoList, setTodoList] = useState([]);
    
    const filteredPhoto = todoList.filter((photo) => {
        return photo.id === props.match.params.id;
    })


    useEffect(() => {
        const todoRef = firebase.database().ref('Todo');
        todoRef.on('value', (snapshot) => {
          console.log(JSON.stringify(snapshot)+" snapshot");
          const todos = snapshot.val(); 
          const todoList = [];
          for (let id in todos) {
            todoList.push({ id, ...todos[id] });
          }
          setTodoList(todoList);          
        });
      }, []);

    return (
        <div>
            <Navigation />
            <Container className={classes.main}>
                {
                    filteredPhoto.length > 0 ?
                        <div>
                            <p className={classes.header}> {filteredPhoto[0].caption}</p>
                            <img src={filteredPhoto[0].imageUrl} className={classes.image} alt='' />
                            <p className={classes.content}> {filteredPhoto[0].firstName} || {filteredPhoto[0].lastName} || {filteredPhoto[0].email} || {filteredPhoto[0].mobile}</p>
                        </div>
                        : <p>No image available</p>
                }
            </Container>
            <Footer />
        </div>
    )
}

export default UserDataDetails;