import { makeStyles, Typography } from '@material-ui/core'
import React from 'react'

const useStyles = makeStyles(theme => ({
    root:{
        // padding: theme.spacing(2, 2),
    }    
  }));
const Top = () => {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <div className="w3-container w3-center header">                
                <h1 className="w3-animate-top">How Mywebsite is different from other Packers and Movers in India?</h1>
                <Typography  className="w3-animate-top"> We are a technology driven company focussed on providing quality, reliable packing and moving services in India to our constomers.</Typography>
            </div>
        </div>
    )
}
export default Top
