import React, { useState, useEffect } from 'react';
import firebase from '../index';
import { Container, Grid, Card, CardMedia, CardContent, Typography, makeStyles, Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';

const useStyles = makeStyles(theme => ({
  heroContent: {
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(8, 0, 6),
  },
  cardGrid: {
      paddingTop: theme.spacing(8),
      paddingBottom: theme.spacing(8),
  },
  card: {
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
  },
  cardMedia: {
      paddingTop: '56.25%',
  },
  cardContent: {
      flexGrow: 1,
  },
  info: {
    fontWeight: 'bold'
  }
  
}));

const UserDataList = (props)=> {
  const classes = useStyles();
  const [todoList, setTodoList] = useState([]);

  useEffect(() => {
    const todoRef = firebase.database().ref('Todo');
    todoRef.on('value', (snapshot) => {
      console.log(JSON.stringify(snapshot)+" snapshot");
      const todos = snapshot.val(); 
      const todoList = [];
      for (let id in todos) {
        todoList.push({ id, ...todos[id] });
      }
      setTodoList(todoList);      
      console.log("ARR2 "+JSON.stringify(todoList));
    });
  }, []);

  const deleteTodo = (id) => {
    const todoRef = firebase.database().ref('Todo').child(id);
    todoRef.remove();
    // toast.error("delete functionality will not work for you!")
  }
  
  const updateTodo = (todo) => {
    console.log("Object to Update" + JSON.stringify(todo));
    toast.info("update functionality coming soon...")
  }

  // const navigateTo = (id) => {
  //   console.log("clicked")
  //   props.history.push(`/users/${id}`)
  // }
  return (
    <div>     
      <Container className={classes.cardGrid} maxWidth="lg">
        <Grid container spacing={4}>
          {todoList.map((card) => (
          <Grid item  xs={12} sm={6} md={4} key={card.id} className="heading animate__animated animate__slideInUp" >
            <Card className={classes.card}  >
                <CardMedia className={classes.cardMedia}
                    image={card.imageUrl}
                    title="Image title" >
                </CardMedia>
                <CardContent className={classes.cardContent}>
                    <Typography gutterBottom variant="h5" component="h2">{card.firstName} {card.lastName}</Typography>
                    <Typography>{card.email}</Typography>                            
                    <Typography>{card.mobile}</Typography> 
                    <Button onClick={() => { deleteTodo(card.id) }}>Delete</Button>
                    <Button onClick={() => { updateTodo(card) }}>Edit</Button>
                    {/* <Button onClick={() => { navigateTo(card.id) }}>More Info</Button> */}
                    <Link to={`/users/${card.id}`} className={classes.info}>More Info...</Link>
                </CardContent>
            </Card>
          </Grid>
          ))} 
        </Grid>
      </Container>
    </div>
  );
}

export default UserDataList