import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import Navigation from '../Navigation/Navigation';
import { Container, TableHead, TableRow, TableCell, Table, TableBody, makeStyles } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(2, 2),
        width: '100%',
        minHeight: '100vh'
    }
}));

const Order = () => {
    const classes = useStyles();
    const [users, setUsers] = useState([]);
   
    useEffect(() => {       
        Axios.get('https://api.randomuser.me/?results=10').then((response) => {
            console.log("Hiii logging..");
            setUsers(response.data.results);
        });
    }, [])

    const orders = users.map((user) => {
        return (<tr key={user.login.uuid}>
            <TableCell >{user.login.uuid}</TableCell>
            <TableCell>{user.name.first}</TableCell>
            <TableCell>{user.name.last}</TableCell>
            <TableCell>{user.email}</TableCell>
            <TableCell>{user.phone}</TableCell>
        </tr>)
    })
   
    return (
        <div>
            <Navigation />
            <Container maxWidth="xl">
                <div className={classes.root}>
                    <Table >
                        <TableHead className="orders">
                            <TableRow >
                                <TableCell >#UUID</TableCell>
                                <TableCell >First Name</TableCell>
                                <TableCell >Last Name</TableCell>
                                <TableCell >Email</TableCell>
                                <TableCell >Phone</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {orders}
                        </TableBody>
                    </Table>
                </div>
            </Container>
        </div>
        )
    }
export default Order;
