import React, { useEffect, useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import { Container } from "@material-ui/core";
import { useFormik } from 'formik';
import { makeStyles } from "@material-ui/styles";
import firebase from 'firebase';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'
import Navigation from "../../components/Navigation/Navigation";

const useStyles = makeStyles(theme => ({    
    error: {
        color: 'red',
        paddingTop: '5px',
        fontWeight: 'bold',
        paddingLeft: '2px'
    }
}));

const UserForm = (props) => {
    console.log(props + "props");
    const classes = useStyles();
    const [todoList, setTodoList] = useState(null);

    const formik = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            email: '',
            mobile: '',
            imageUrl: ''
        },
        onSubmit: values => {
            console.log('Contact us request ', values);
            const todoRef = firebase.database().ref('Todo');
           
            todoRef.push(values);

            setTodoList(values,...todoList )
            // toast("Hi");
            toast.info("submit successfully " , { position: toast.POSITION.TOP_RIGHT, autoClose: 3000 });
        },
        validate: values => {
            let errors = {};

            if (!values.firstName) {
                errors.firstName = "First name is required ";
            } else if (values.firstName.length < 3) {
                errors.firstName = 'First Name can not be less than 3 chararcter';
            }
            if (!values.lastName) {
                errors.lastName = "Last name is required";
            }
            if (!values.email) {
                errors.email = "Email is required";
            } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
                errors.email = "Invalid email address";
            }
            if (!values.mobile) {
                errors.mobile = "Mobile is required";
            }
            if (!values.imageUrl) {
                errors.imageUrl = "Image Url is required";
            }
            return errors;
        }
    });

    useEffect(() => {
        const todoRef = firebase.database().ref('Todo');
        todoRef.on('value', (snapshot) => {
          const todos = snapshot.val();
          const todoList = [];
          for (let id in todos) {
            todoList.push({ id, ...todos[id] });
          }
          setTodoList(todoList);
        });
      }, []);

    return (
        <div>
            <Navigation/>
            <Container maxWidth="md">
                <h1 className="enquiry">Drop Your Message</h1>
                <form onSubmit={formik.handleSubmit} noValidate>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                fullWidth
                                required
                                id="firstName"
                                name="firstName"
                                label="First Name"
                                placeholder="First Name"
                                autoComplete="firstName"
                                onChange={formik.handleChange}
                                value={formik.values.firstName}
                                autoFocus
                            />
                            {formik.errors.firstName ? <div className={classes.error}>{formik.errors.firstName}</div> : null}
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                fullWidth
                                required
                                id="lastName"
                                name="lastName"
                                label="Last Name"
                                placeholder="Last Name"
                                autoComplete="lastName"
                                onChange={formik.handleChange}
                                value={formik.values.lastName}
                            />
                            {formik.errors.lastName ? <div className={classes.error}>{formik.errors.lastName}</div> : null}
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                fullWidth
                                required
                                id="email"
                                name="email"
                                label="Email"
                                placeholder="Email"
                                autoComplete="email"
                                onChange={formik.handleChange}
                                value={formik.values.email}
                            />
                            {formik.errors.email ? <div className={classes.error}>{formik.errors.email}</div> : null}
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                fullWidth
                                required
                                id="mobile"
                                name="mobile"
                                label="Mobile"
                                placeholder="Mobile"
                                autoComplete="mobile"
                                onChange={formik.handleChange}
                                value={formik.values.mobile}
                            />
                            {formik.errors.mobile ? <div className={classes.error}>{formik.errors.mobile}</div> : null}
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                fullWidth
                                required
                                id="imageUrl"
                                name="imageUrl"
                                label="Image Url"
                                placeholder="Image Url"
                                autoComplete="imageUrl"
                                onChange={formik.handleChange}
                                value={formik.values.imageUrl}
                            />
                            {formik.errors.imageUrl ? <div className={classes.error}>{formik.errors.imageUrl}</div> : null}
                        </Grid>
                        <Button type="submit" fullWidth variant="contained"  color="primary">Send</Button>
                    </Grid>
                </form>            
            </Container>
        </div>
    );
}
export default UserForm;
