import React from 'react';
import { MDBContainer, MDBRow, MDBCol, MDBBtn } from 'mdbreact';
import { makeStyles } from '@material-ui/core';
import { useFormik } from 'formik';

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(5, 2),
        paddingLeft: '30%',
        paddingTop: '10%',
        backgroundImage: 'https://images.unsplash.com/photo-1589555477097-c1acbf330b95?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max'
    }
}));

const SignIn2 = () => {
    const classes = useStyles();

    const formik = useFormik({
        initialValues: {
            email: '',
            password: ''
        },
        onSubmit: values => {
            console.log('Login Request2 ', JSON.stringify(values));
            alert("Login Request2 " + JSON.stringify(values, null, 2))
        }
    });

    return (
        <div className={classes.root}>
            <MDBContainer>
                <MDBRow>
                    <MDBCol md="6">
                        <form onSubmit={formik.handleSubmit} noValidate>
                            <p className="h4 text-center mb-4">Sign in</p>
                            <label htmlFor="defaultFormLoginEmailEx" className="grey-text">Your email</label>
                            <input
                                type="email"
                                id="defaultFormLoginEmailEx"
                                className="form-control"
                                name="email"
                                onChange={formik.handleChange}
                                value={formik.values.email}                                        
                            />
                            <br />
                            <label htmlFor="defaultFormLoginPasswordEx" className="grey-text">Your password</label>
                            <input
                                type="password"
                                id="defaultFormLoginPasswordEx"
                                className="form-control"
                                name="password"
                                onChange={formik.handleChange}
                                value={formik.values.password}                                        
                            />
                            <div className="text-center mt-4"><MDBBtn color="indigo" type="submit">Login</MDBBtn></div>
                        </form>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </div>
    );
};

export default SignIn2;


