import React from 'react';
import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import Navigation from '../../components/Navigation/Navigation';
import { Container, makeStyles } from '@material-ui/core';
import Footer from '../../components/Footer/Footer';
import AlertSample from './AlertSample';

const useStyles = makeStyles(theme => ({
    "@global": {
        body: {
            backgroundColor: theme.palette.common.white
        }
    },
    root: {
        marginTop: theme.spacing(8),
        flexDirection: 'column',
        alignItems: 'center',
        minHeight: '70vh',
        display: 'inline-flex',
    }
}));

const ToastComponent = (props) => {

    const classes = useStyles();
    const simpleToast = () => {
        toast('Hiii', { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
    }
    const infoToast = () => {
        toast.info('Hiii', { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
    }
    const warnToast = () => {
        toast.warn('Hiii', { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
    }
    const successToast = () => {
        toast.success('Hiii', { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
    }
    const errorToast = () => {
        toast.error('Hiii', { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
    }
    const stickToast = () => {
        toast('Hiii', { position: toast.POSITION.TOP_CENTER, autoClose: false });
    }
    return (
        <div>
            <Navigation />
            <Container className={classes.root}>
                <div>
                    <button onClick={simpleToast}>Simple Toast</button>
                    <button onClick={infoToast}>Info Toast</button>
                    <button onClick={warnToast}>Warn Toast</button>
                    <button onClick={successToast}>Success Toast</button>
                    <button onClick={errorToast}>Error Toast</button>
                    <button onClick={stickToast}>Stick Toast</button>
                    <ToastContainer />
                </div>
                <AlertSample></AlertSample>
            </Container>
            <Footer />
        </div>
    );
};

export default ToastComponent;