import React from "react";
import { Link } from "react-router-dom";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Footer from "../components/Footer/Footer";
import { useFormik } from "formik";
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import Axios from "axios";

const useStyles = makeStyles(theme => ({
    "@global": {
        body: {
            backgroundColor: theme.palette.common.white
        }
    },
    main: {
        minHeight: '66vh',
        margin: '10px',
        alignContent: 'center'
    },
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center"
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main
    },
    form: {
        width: "100%",
        marginTop: theme.spacing(1)
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },
    error: {
        color: 'red',
        paddingTop: '5px',
        fontWeight: 'bold',
        paddingLeft: '2px'
    }
}));

const SignIn = (props) => {
    const classes = useStyles();

    const navigateTo = () => {
        console.log("you are just loggedIn")
        props.history.push(`/userList`)
    }

    const formik = useFormik({
        initialValues: {
            email: '',
            password: ''
        },
        onSubmit: values => {
            console.log('Login request ', values);
            Axios.post('http://localhost:9090/public/login', values).then((response) => {
                console.log(JSON.stringify(response.data));                
                toast.success('Hi ' + values.email + ' Login successfull', { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
                navigateTo();    
            }).catch(error => {                
                toast.error("Login error | Please try Later" , { position: toast.POSITION.TOP_RIGHT, autoClose: 10000 });
            })   
        },
        validate: values => {
            let errors = {};
            if (!values.email)
                errors.email = "Email is required";
            else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email))
                errors.email = "Invalid email address";
            if (!values.password)
                errors.password = "Password is required";
            return errors;
        }
    })

    return (
        <div >
            <Container component="main" maxWidth="sm" >
                <div className={classes.paper}>
                    <Avatar className={classes.avatar}></Avatar>
                    <Typography component="h1" variant="h5">Sign in</Typography>
                    <form className={classes.form} onSubmit={formik.handleSubmit}>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            id="email"
                            label="Email Address"
                            name="email"
                            autoComplete="email"
                            onChange={formik.handleChange}
                            value={formik.values.email}
                            autoFocus
                        />
                        {formik.errors.email ? <div className={classes.error}>{formik.errors.email}</div> : null}
                        <TextField
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            onChange={formik.handleChange}
                            value={formik.values.password}
                            autoComplete="current-password"
                        />
                        {formik.errors.password ? <div className={classes.error}>{formik.errors.password}</div> : null}
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}>
                            Sign In
                        </Button>

                        <Grid container>
                            <Grid item xs><Link to="/admin/forgetpassword" variant="body2">Forgot password?</Link></Grid>
                            <Grid item>
                                <Link to="/admin/signup" variant="body2">
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                </div>
                <Footer />
            </Container>
        </div>
    );
}
export default SignIn