import React from 'react';
import Typography from '@material-ui/core/Typography';
import './Header.css';
import Container from '@material-ui/core/Container';


export default function Header() {
    return (
        <Container maxWidth="lg">
            <div className="header">
                <h3 >What Mywebsite do?</h3>
                <Typography component="p">Mywebsite offers professional packing and moving services for all home shifting and office relocation in India and foreign country also. </Typography>
                {/* Whether it is an inter city move or a local shift, our reliable packing and moving team has you covered. */}
            </div>
        </Container>
    );
}