import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Footer from "../components/Footer/Footer";
import { useFormik } from "formik";
import {toast} from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

const useStyles = makeStyles(theme => ({
    "@global": {
        body: {
            backgroundColor: theme.palette.common.white
        }
    },
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        minHeight: '20vh'
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main
    },
    form: {
        width: "100%",
        marginTop: theme.spacing(1),
        minHeight: '40%'
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },
    error: {
        color: 'red',
        paddingTop: '5px',
        fontWeight: 'bold',
        paddingLeft: '2px'
    }

}));

export default function SignIn() {
    const classes = useStyles();

    const formik = useFormik({
        initialValues: {
            email: ''
        },
        onSubmit: values => {
            toast.info('Recovery email has been sended')
            values.email ='';
        },
        validate: values => {
            let errors = {};
            if (!values.email) {
                errors.email = "Email is required";
            } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
                errors.email = "Invalid email address";
            }
            return errors;
        }
    })

    return (
        <Container component="main" maxWidth="sm">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    {/* <LockOutlinedIcon /> */}
                </Avatar>
                <Typography component="h1" variant="h5">Forget Password</Typography>
                <form className={classes.form} onSubmit={formik.handleSubmit} noValidate>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="email"
                        name="email"
                        label="Email Address"
                        placeholder="Email Address"
                        autoComplete="email"
                        onChange={formik.handleChange}
                        value={formik.values.email}
                        autoFocus
                    />
                    {formik.errors.email ? <div className={classes.error}>{formik.errors.email}</div> : null}
                    <Button type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        disabled={formik.errors.email || !formik.values.email}>Send
                    </Button>
                    <Grid container>
                        <Grid item >
                            <Link to="/admin/login" variant="body2">Back to Login</Link>
                        </Grid>
                    </Grid>
                </form>
            </div>
            <Footer />
        </Container>
    );
}
