import React from "react";
import Content from "../../../content"
import Footer from "../../Footer/Footer";
import Navigation from "../../Navigation/Navigation";
import { makeStyles } from "@material-ui/styles";
import { Container } from "@material-ui/core";


const useStyles = makeStyles(theme => ({
    main: {
        minHeight: '100vh',
        // margin: '10px',
        alignContent: 'center'
    },
    header: {
        fontWeight: 'bold'
    },
    content: {
        paddingLeft: '10px',
        marginTop: '10px'
    },
    image: {
        width: '40%',
        height: "10%"
    }

}));

const PhotoDetail = (props) => {

    console.log(props)
    const classes = useStyles();
    const filteredPhoto = Content.album.cards.filter((photo) => {
        return photo.id === parseInt(props.match.params.photoId)
    })

    return (
        <div>
            <Navigation />
            <Container className={classes.main}>
                {
                    filteredPhoto.length > 0 ?
                        <div>
                            <p className={classes.header}> {filteredPhoto[0].caption}</p>
                            <img src={filteredPhoto[0].imageUrl} className={classes.image} alt='' />
                            <p className={classes.content}> {filteredPhoto[0].description}</p>
                        </div>
                        : <p>No image available</p>
                }
            </Container>
            <Footer />
        </div>
    )
}

export default PhotoDetail;