import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {storage} from './index'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'
import Navigation from '../components/Navigation/Navigation';
import { Container, Button } from '@material-ui/core';

const useStyles = makeStyles(theme => ({    
    root: {
        padding: theme.spacing(2, 2),
        width: '100%',
        minHeight: '100vh'
    },
    form: {
        width: '100%',
        marginTop: theme.spacing(10),
        minHeight: '40%'
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },heroContent: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(8, 0, 6),
    },
    
}));


const  ImageUpload = (props) =>{
    
    const classes = useStyles();
    const [image,setImage] = useState(null);
    const [link,setLink] = useState(null);

    

    const handleChange = e => {
        console.log(props);
        if(e.target.files[0]){
            setImage(e.target.files[0]);
        }
    }

    const handleUpload = () => {
        console.log("Image"+ image );
        const uploadTask = storage.ref(`images/${image.name}`).put(image);
        uploadTask.on(
            "state_changed",
            snapshot =>{},
            error => {
                console.log(error);
                toast.error("File upload failed ", { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
            },
            ()=>{
                storage.ref("images").child(image.name)
                .getDownloadURL().then(url=>{
                    console.log(url);
                    toast.info("File uploaded successfully " , { position: toast.POSITION.TOP_RIGHT, autoClose: 5000 });
                    setLink(url);
                })
            }
        )
    }

    
    return (
        <div>
           <Navigation/>
            <Container component="main" maxWidth="sm">
                <div className={classes.root}>
                    <div className={classes.form}>
                    <input type="file" onChange={handleChange}/>
                    <Button onClick={handleUpload}>Upload</Button>                   
                    </div>
                    <p>{link}</p>
                </div>
            </Container>
            
        </div>
        
    );
}

export default ImageUpload;