import React from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { useFormik } from "formik";
import Footer from "../Footer/Footer";
import Navigation from "../Navigation/Navigation";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'

toast.configure();
const useStyles = makeStyles(theme => ({    
    root: {
        padding: theme.spacing(2, 2),
        width: '100%',
        minHeight: '100vh'
    },
    form: {
        width: '100%',
        marginTop: theme.spacing(10),
        minHeight: '40%'
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },
    error: {
        color: 'red',
        paddingTop: '5px',
        fontWeight: 'bold',
        paddingLeft: '2px'
    }
}));

const Help = () => {
    const classes = useStyles();

    const notify = () => {
        // toast('Your query is submitted successfully');
        // toast('Your query is submitted successfully');
    }
    const formik = useFormik({
        initialValues: {
            mobile: ''
        },
        onSubmit: values => {            
            toast.info("You are searching for " + values.mobile, { position: toast.POSITION.TOP_RIGHT, autoClose: 10000 });
            // toast("You are searching for " + values.mobile, { position: toast.POSITION.TOP_CENTER, autoClose: false });
            // toast.success("You are searching for " + values.mobile, { position: toast.POSITION.TOP_RIGHT, autoClose: 12000 });
            // toast.error("You are searching for " + values.mobile, { position: toast.POSITION.BOTTOM_LEFT });
            // toast.warn("You are searching for " + values.mobile, { position: toast.POSITION.BOTTOM_CENTER, autoClose: 6000 });
            // toast("You are searching for " + values.mobile, { position: toast.POSITION.BOTTOM_RIGHT, autoClose: 8000 });
        },
        validate: values => {
            let errors = {};
            if (!values.mobile)
                errors.mobile = 'Mobile is required';
            else if (values.mobile.length < 10)
                errors.mobile = 'Invalid mobile number';
            else if (values.mobile.length > 10)
                errors.mobile = 'Invalid mobile number';
            return errors;
        }
    })

    return (
        <div>
            <Navigation />
            <Container component="main" maxWidth="sm">
                <div className={classes.root}>
                    <div className={classes.form}>
                    <Typography component="h1" variant="h5">Track your booking</Typography>
                    <form onSubmit={formik.handleSubmit} noValidate>
                        <TextField
                            variant="outlined"
                            fullWidth
                            id="mobile"
                            name="mobile"
                            label="Mobile"
                            placeholder="Mobile"
                            autoComplete="mobile"
                            onChange={formik.handleChange}
                            value={formik.values.mobile}
                            autoFocus />
                        {formik.errors.mobile ? <div className={classes.error}>{formik.errors.mobile}</div> : null}
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            onClick={notify}
                            className={classes.submit} disabled={formik.errors.mobile || !formik.values.mobile}>Search</Button>
                    </form>
                    </div>
                </div>
            </Container>
            <Footer />
        </div>
    );

};


export default Help;
