import React, { useState, useEffect } from 'react';
import Footer from '../Footer/Footer';
import { Table, TableHead, Container, TableRow, TableCell, TableBody, makeStyles } from '@material-ui/core';
import Axios from 'axios';
import Navigation from '../Navigation/Navigation';

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(2, 2),
        width: '100%',
        minHeight: '100vh'
    }
}));

const UserList = () => {
    const classes = useStyles();
    const [users, setUsers] = useState([]);

    useEffect(() => {        
        Axios.get('https://api.randomuser.me/?results=10').then((response) => {
            console.log("Hiii logging..");
            setUsers(response.data.results);
        });
    }, [])
   
    const userData = users.map(user => {
        return (<TableRow key={user.id}>
            <TableCell >{user.login.uuid}</TableCell>
            <TableCell>{user.name.first}</TableCell>
            <TableCell>{user.name.last}</TableCell>
            <TableCell>{user.email}</TableCell>
            <TableCell>{user.phone}</TableCell>
            <TableCell><img src={user.picture.large} alt='' /></TableCell>
        </TableRow>)
    })

    return (
        <div>
            <Navigation />
            <Container maxWidth="md">
                <div className={classes.root}>
                    <Table>
                        <TableHead>
                            <TableRow>                            
                                <TableCell >First Name</TableCell>
                                <TableCell >Last Name</TableCell>
                                <TableCell >Email Id</TableCell>
                                <TableCell >Username</TableCell>
                                <TableCell >Phone</TableCell>
                                <TableCell >Profile</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {userData}
                        </TableBody>
                    </Table>
                </div>
            </Container>
            <Footer />
        </div>
    );
};

export default UserList;