
import React, { useState, useEffect } from 'react';
import { Container, Grid, Card, CardMedia, makeStyles } from '@material-ui/core';
import {storage} from './index'
const useStyles = makeStyles(theme => ({    
    root: {
        padding: theme.spacing(2, 2),
        width: '100%',
        minHeight: '100vh'
    },
    form: {
        width: '100%',
        marginTop: theme.spacing(10),
        minHeight: '40%'
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },heroContent: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(8, 0, 6),
    },
    cardGrid: {
        paddingTop: theme.spacing(8),
        paddingBottom: theme.spacing(8),
    },
    card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
    },
    cardMedia: {
        paddingTop: '56.25%',
    },
    cardContent: {
        flexGrow: 1,
    },
    info: {
        fontWeight: 'bold'
    }
}));
const ImageList = () => {
    const classes = useStyles();
    const [imageUrl, setImageUrl] = useState([]);
    useEffect(() => {  
        var i =0;     
        storage.ref('images/').listAll().then(function(result){
            result.items.forEach(function(imageRef){
                i++;
                displayImage(i,imageRef);
            })
        })            
    }, [])

    const  displayImage= (i,images)=>{
        images.getDownloadURL().then(function(url){
            setImageUrl(existingArr => [...existingArr,url]);
        })       
    }
    return (
        <div>
            
            <Container className={classes.cardGrid} >
                    <Grid container spacing={4}>
                        {imageUrl.map((card,index) =>{                       
                        return (
                            <Grid item  xs={12} sm={6} md={4} key={index} >
                                <Card className={classes.card}>
                                    <CardMedia className={classes.cardMedia}
                                        image={card}
                                        title="Image title" />
                                </Card>
                            </Grid>
                        )} )}                        
                    </Grid>
            </Container>
        </div>
    );
};

export default ImageList;