import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Typography } from '@material-ui/core';
import users from '../Home/User';

const useStyles = makeStyles(theme => ({
    profile: {
        backgroundColor: '#e3f2fd',
        paddingTop: theme.spacing(2)
    },
    managementTeam: {
        textAlign: "center",
        color: 'gray',
        fontSize: '20px',
        fontWeight: 'bold'
    },    
    userImage: {
        maxWidth: '100%',
        maxHeight: '100%',
    },
    description:{
        padding:'15px'
    }
}));

function ManagementProfile() {
    const classes = useStyles();

    return (
        <div className={classes.profile}>
            <Typography className={classes.managementTeam}>Our Team</Typography>
            <p className={classes.description}>
            Mywebsite serves clients through its well qualified team members and robust infrastructure facilities. We are the leading packers and movers company in Delhi. Our vast fleet of customized, innovative and efficient services has made us the most popular and reliable service provider in the city.
            </p>
            <Grid container>
                {users.map((user) => (
                    <Grid item xs={3} key={user.id}>
                        <img className={classes.userImage} src={user.imageLink} alt='' />
                    </Grid>
                ))}
            </Grid>
        </div>
    );
}
export default ManagementProfile;
