import React from 'react';
import SimpleImageSlider from "react-simple-image-slider";
import { Container } from '@material-ui/core';

const SimpleSlider = () => {

    const images = [
        { url: "ejaz.jpg" },
        { url: "shahzad.jpg" },
        { url: "ejaz.jpg" },
        { url: "shahzad.jpg" },
        { url: "ejaz.jpg" }        
    ];
    return (
        <div>
            <Container width="sm">
                <SimpleImageSlider
                        width={1000}
                        height={700}
                        images={images}>            
                </SimpleImageSlider>
            </Container>
        </div>
    );
};

export default SimpleSlider;